
import admin from 'firebase-admin';
import { App, getApp, getApps, initializeApp } from 'firebase-admin/app';

function initializeAdminApp(): App {
  if (getApps().length > 0) {
    return getApp();
  }

  // Check if the service account credentials are provided as a JSON string
  const serviceAccountJson = process.env.GOOGLE_APPLICATION_CREDENTIALS;
  if (!serviceAccountJson) {
    console.error(
      'Firebase Admin SDK service account credentials are not set in the GOOGLE_APPLICATION_CREDENTIALS environment variable.'
    );
    throw new Error('Firebase Admin SDK service account credentials are not set.');
  }

  try {
    const credentials = JSON.parse(serviceAccountJson);
    return initializeApp({
      credential: admin.credential.cert(credentials),
    });
  } catch (e: any) {
    console.error('Failed to parse Firebase Admin SDK service account credentials.', e.message);
    throw new Error('Firebase Admin SDK initialization failed due to malformed credentials.');
  }
}

let adminApp: App;
try {
  adminApp = initializeAdminApp();
} catch (e: any) {
  console.error('CRITICAL: Firebase Admin SDK failed to initialize.', e.message);
  // We throw here to prevent the app from running in a broken state.
  throw e;
}

export const adminDb = admin.firestore();
